<template>
	<div class="city-one">
        <Header class="city_one-header">
            <router-link tag="div" to='/' slot="first">ele.me</router-link>
            <div slot="mid"></div>
        </Header>
        <div>
            <div>
        <div class="city-div2">
            <p><span>当前定位城市:</span>  <span>定位不准时，请在城市列表中选择</span></p>
            <p>
                <span>{{this.list.name}}</span>
                <span @click="Search()" class="el-icon-arrow-right"></span>
            </p>
        </div>

        <div class="city-div3"></div>

        <div class="city-div4">
            <p>热门城市</p>
            <ul>
                <router-link tag="li" v-for="(item,index) in arr" 
                :to="{name:'SeekCity',query:{SeekCity:item.id}}" :key="index">{{item.name}}</router-link>
            </ul>
        </div>

        <div class="city-div3"></div>

        <div v-for="(item,index) in arrtow" :key="index" class="city-div4">
            <p>{{item}}(按字母排序)</p>
            <ul>
                <li v-for="(i,k) in array[item]" :key="k">
                <router-link :to="{name:'SeekCity',query:{SeekCity:i.id}}" :style="{color:'#000'}">{{i.name}}</router-link>
                </li>
            </ul>
        </div>
</div>
</div>
</div>
</template>

<script>
import Header from '@/components/header.vue'
export default {
    name: "city-one",
    components:{
        Header
    },
    data(){
        return {
            list:[],
            arr:[],
            array:[],
            arrtow:[]
        }
    },
    methods:{
        Search(){
            this.$router.push({
                name:'SeekCity',params:{name:this.list.name}
            })
        },
        async Acquire(){
            this.axios.get("http://elm.cangdu.org/v1/cities?type=guess").then((res)=>{
                // console.log(res)
                this.list=res.data
            })
            this.axios.get("http://elm.cangdu.org/v1/cities?type=hot").then((s)=>{
                //console.log(s)
                this.arr=s.data
            })
             this.axios.get("http://elm.cangdu.org/v1/cities?type=group").then((s)=>{
                //console.log(s)
                this.arrtow=Object.keys(s.data).sort()
                this.array=s.data
                //console.log(this.array)
            })
        },
    },
    mounted(){
        this.Acquire()
    }
}
</script>

<style lang='scss' scope>
a{
  text-decoration: none;
}

.city-div2,ul,li,p{margin: 0;padding: 0;} 
ul,li{list-style: none;}
.city-one{
    width: 100%;height: 100%;
    display: flex;
    flex-direction: column;
    >div:nth-of-type(2){
        overflow: auto;
        flex-grow: 1;
    }
}
.city_one-header{
    position: fixed;
    top: 0;
  z-index: 100;
}
.city-div2{
    height: 1.4rem;
    width: 100%;height: 0.74rem;
    font-size: 0.22rem;
    p:nth-of-type(1){
        height: 50%;
        font-size: 0.2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: 6C6C6C;
        box-sizing: border-box;
        border-bottom:0.01rem solid #E3E3E3;
    }
    p:nth-of-type(2){
        height: 50%;
        font-size: 0.3rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        span:nth-of-type(1){
            color: #3792E5;
        }
    }
}
.city-div3{
    width: 100%;height: 0.2rem;
    box-sizing: border-box;
    background: #F5F5F5;
    border-top: 0.02rem solid #E4E4E4;
    border-bottom: 0.02rem solid #E4E4E4;
}
.city-div4{
    width: 100%;
    p{
        // height: 0.55rem;
        line-height: 0.54rem;
    }
    ul{
        display: flex;
        flex-wrap: wrap;
        li{
            width: 25%;height: 0.7rem;
            text-align: center;
            line-height: 0.7rem;
            box-sizing: border-box;
            border: 0.01rem solid  #E3E3E3;
            color: #3792E5;
            a{color: #3792E5;}
        }
    }
}

</style>


